import React, { useState, useEffect } from 'react';
import axios from 'axios';
import LoginPage from './LoginPage';
import StatusPage from './StatusPage';
import SelectCompany from './SelectCompany';
import { Backdrop } from '@mui/material';
import CircularProgress from '@mui/material/CircularProgress';
import JobProcesses from './JobProcesses';

export const ProcessTypes = {
  COMPANY: 'select company',
  CREATE: 'create opportunity',
  SELECTJOB: 'Selecting Job',
  SELECTPROCESS: 'Selecting Process',
  PUBLISH: 'Publish Opportunity'
}

const defaultCareerOpportunity = {
  jobs: [],
  processes: [],
  skills: [],
  skillLevels: [],
  questions: []
}

const defaultPairs = {
  job: '',
  process: '',
  selectedQuestions: [],
  selectedSkills: [{ skill: '', level: '' }]
}


function JobsProcessesPage({ name }) {
  const [careerOpportunity, setCareerOpportunity] = useState(defaultCareerOpportunity)
  const [loading, setLoading] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [error, setError] = useState(null);
  const [processType, setProcessType] = useState(ProcessTypes.CREATE);
  const [pairs, setPairs] = useState([{
    job: '',
    process: '',
    selectedQuestions: [],
    selectedSkills: [{ skill: '', level: '' }]
  }
  ]);
  const [statuses, setStatuses] = useState([]);
  const [availableCompanies, setAvailableCompanies] = useState([])
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [company, setCompany] = useState('')


  useEffect(() => {
    const tryTheLogin = async () => {
      try {
        setLoading(true);
        if (name) {
          const response = await axios.post('https://api.milesahead.team/api/login-with-saved-credentials', { name });
          if (response.data.message.companies) {
            setAvailableCompanies(response.data.message.companies)
            setIsLoggedIn(true)

          }
        }
      } catch (error) {
        setError('Error fetching jobs and processes');
        console.error(error);
      } finally {
        setLoading(false);
      }
    };

    tryTheLogin();
  }, [name]);

  const handlePairChange = (index, field, value) => {
    const newPairs = [...pairs];
    newPairs[index][field] = value;
    setPairs(newPairs);
  };

  // const handleQuestionChange = (index, selectedQuestions) => {
  //   const newPairs = [...pairs];
  //   newPairs[index].selectedQuestions = selectedQuestions;
  //   setPairs(newPairs);
  // };

  // const handleSkillChange = (pairIndex, skillIndex, field, value) => {
  //   const newPairs = [...pairs];
  //   newPairs[pairIndex].selectedSkills[skillIndex][field] = value;
  //   setPairs(newPairs);
  // };

  // const handleAddSkill = (index) => {
  //   const newPairs = [...pairs];
  //   newPairs[index].selectedSkills.push({ skill: '', level: '' });
  //   setPairs(newPairs);
  // };

  const handleAddPair = () => {
    setPairs([...pairs, { job: '', process: '', selectedQuestions: [], selectedSkills: [{ skill: '', level: '' }] }]);
  };

  const handleDeletePair = (opportunityIndexToDelete) => {
    const totalPairs = [...pairs];
    totalPairs.splice(opportunityIndexToDelete, 1);
    setPairs(totalPairs);
  };

  const handleCompanyClick = async (company) => {
    setLoading(true);
    try {
      setCompany(company);
      const response = await axios.post('https://api.milesahead.team/api/get-jobs-and-processes', { name, company });
      const { jobsAndProcesses } = response.data;
      setCareerOpportunity({
        jobs: jobsAndProcesses.jobs || [],
        processes: jobsAndProcesses.processes || [],
        skills: jobsAndProcesses.skills || [],
        skillLevels: jobsAndProcesses.skillLevels || [],
        questions: jobsAndProcesses.questions || []
      })
      setProcessType(ProcessTypes.CREATE);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const resetCareerSelection = () => {
    let initialPair = Object.assign({}, defaultPairs);

    setProcessType(ProcessTypes.CREATE);
    setIsSubmitted(false);
    setStatuses([])
    setPairs([initialPair]);

  }

  const handleSubmit = async () => {
    try {
      if (name && pairs.length > 0) {
        setIsSubmitted(true)
        // Generate all possible combinations of jobs and processes
        // const allPairs = jobs.flatMap(job => processes.map(process => ({ job, process: process.value, selectedQuestions: [], selectedSkills: [{ skill: '', level: '' }] })));
        // Generate one pair for each job with the first process
        //  const jobWithFirstProcess = careerOpportunity.jobs.map(job => ({ job, process: careerOpportunity.processes[0].value, selectedQuestions: [], selectedSkills: [{ skill: '', level: '' }] }));
        const response = await axios.post('https://api.milesahead.team/api/create-opportunities', {
          name,
          company,
          //couples: jobWithFirstProcess,
          couples: pairs.map(pair => ({
            job: pair.job,
            process: pair.process,
            selectedQuestions: pair.selectedQuestions,
            selectedSkills: pair.selectedSkills
          })),
        });
        const statuses = response.data.statuses;
        setStatuses(statuses);
      }
    } catch (error) {
      console.error('Error processing choices', error);
    }
  };

  if (loading) {
    return (
      <Backdrop
        sx={(theme) => ({ color: '#fff', zIndex: theme.zIndex.drawer + 1 })}
        open={loading}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
    )
  }

  // if (loading) {
  //   return <div>Loading...</div>;
  // }

  if (!isLoggedIn || error || name === undefined) {
    return <LoginPage name={name} setAvailableCompanies={setAvailableCompanies} setIsLoggedIn={setIsLoggedIn} isLoggedIn={isLoggedIn} />;
  }

  if ((availableCompanies && !company) || processType === ProcessTypes.COMPANY) {
    return <SelectCompany availableCompanies={availableCompanies} handleCompanyClick={handleCompanyClick} />
  }
  // // 2nd Page - Company Selection page
  // if (availableCompanies && !company) {
  //   return (
  //     <div>
  //       <h1>Company Selection</h1>
  //       <div>
  //         {availableCompanies.map((company, index) => (
  //           <button
  //             key={index}
  //             onClick={() => handleCompanyClick(company)}
  //             style={{
  //               background: 'none',
  //               border: 'none',
  //               color: 'blue',
  //               textDecoration: 'underline',
  //               cursor: 'pointer',
  //               marginRight: '10px'
  //             }}
  //           >
  //             {company}
  //           </button>
  //         ))}
  //       </div>
  //     </div>
  //   )
  // }


  if (statuses && isSubmitted && processType === ProcessTypes.PUBLISH) {
    return <StatusPage
      statuses={statuses}
      name={name}
      company={company}
      resetProcess={resetCareerSelection}
    />;
  }

  return (
    <JobProcesses
      jobs={careerOpportunity.jobs}
      processes={careerOpportunity.processes}
      pairs={pairs}
      handleAddPair={handleAddPair}
      handlePairChange={handlePairChange}
      handleDeletePair={handleDeletePair}
      handleSubmit={handleSubmit}
      processType={processType}
      setProcessType={setProcessType}
    />
  );

  // return (
  //   <div>
  //     <h1>Select Jobs and Processes</h1>
  //     {pairs.map((pair, index) => (
  //       <div
  //         key={index}
  //         style={{
  //           border: '1px solid #ccc',
  //           padding: '10px',
  //           marginBottom: '10px',
  //           borderRadius: '5px'
  //         }}
  //       >
  //         {/* Job Dropdown */}
  //         <select
  //           value={pair.job}
  //           onChange={(e) => handlePairChange(index, 'job', e.target.value)}
  //         >
  //           <option value="">Select Job</option>
  //           {jobs.map((job, i) => (
  //             <option key={i} value={job}>
  //               {job}
  //             </option>
  //           ))}
  //         </select>

  //         {/* Process Dropdown */}
  //         <select
  //           value={pair.process}
  //           onChange={(e) => handlePairChange(index, 'process', e.target.value)}
  //         >
  //           <option value="">Select Process</option>
  //           {processes.map((process, i) => (
  //             <option key={i} value={process.value}>
  //               {process.text}
  //             </option>
  //           ))}
  //         </select>

  //         {/* Show additional fields (Questions and Skills) only if job and process are selected */}
  //         {false && pair.job && pair.process && ( // Added False so that it won't show
  //           <div>
  //             {/* Questions Dropdown (Multiple Selection) */}
  //             <div>
  //               <h4>Select Questions</h4>
  //               <select
  //                 multiple
  //                 value={pair.selectedQuestions}
  //                 onChange={(e) =>
  //                   handleQuestionChange(index, [...e.target.selectedOptions].map(opt => opt.value))
  //                 }
  //               >
  //                 {questions.map((question, questionIndex) => (
  //                   <option key={questionIndex} value={question}>
  //                     {question}
  //                   </option>
  //                 ))}
  //               </select>
  //             </div>

  //             {/* Skills and Skill Levels (Multiple Skills with Levels) */}
  //             <div>
  //               <h4>Select Skills and Levels</h4>
  //               {pair.selectedSkills.map((selectedSkill, skillIndex) => (
  //                 <div key={skillIndex}>
  //                   {/* Skill Dropdown */}
  //                   <select
  //                     value={selectedSkill.skill}
  //                     onChange={(e) => handleSkillChange(index, skillIndex, 'skill', e.target.value)}
  //                   >
  //                     <option value="">Select Skill</option>
  //                     {skills.map((skill) => (
  //                       <option key={skill.value} value={skill.value}>
  //                         {skill.text}
  //                       </option>
  //                     ))}
  //                   </select>

  //                   {/* Skill Level Dropdown */}
  //                   <select
  //                     value={selectedSkill.level}
  //                     onChange={(e) => handleSkillChange(index, skillIndex, 'level', e.target.value)}
  //                   >
  //                     <option value="">Select Skill Level</option>
  //                     {skillLevels.map((level) => (
  //                       <option key={level.value} value={level.value}>
  //                         {level.text}
  //                       </option>
  //                     ))}
  //                   </select>
  //                 </div>
  //               ))}
  //               <button onClick={() => handleAddSkill(index)}>Add Skill</button>
  //             </div>
  //           </div>
  //         )}
  //       </div>
  //     ))}
  //     <button onClick={handleAddPair}>Add Another Pair</button>
  //     <button onClick={handleSubmit}>Submit</button>
  //   </div>
  // );
}

export default JobsProcessesPage;
