import React, { useState } from 'react';
import { Box, Button, FormControl, FormHelperText, MenuItem, Select, Typography } from '@mui/material';
import colors from '../../styles/colors';
import Header from './components/header';

function SelectCompany({ availableCompanies, handleCompanyClick }) {
  const [companyName, setCompanyName] = useState('')
  const [isValidForm, setValidation] = useState(true)

  return (
    <>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'stretch', }}>
        <Header title={'Select a Company'}
        />
        <FormControl size='small'
          sx={{ p: '20px', alignSelf: 'center' }}
          display='flex'
          flexDirection={'column'}
          error={!isValidForm && companyName === ''}
        >
          <Typography variant="caption" color="textSecondary" fontWeight='bold'>
            Select Company*
          </Typography>
          <Select
            sx={{ width: '70vw', mt: '5px' }}
            margin="dense"
            labelId="select-company"
            id="select-company"
            value={companyName}
            onChange={(e) => setCompanyName(e.target.value)}
          >
            {availableCompanies.map((company, index) => (
              <MenuItem key={index} value={company}>{company}</MenuItem>
            ))}
          </Select>
          {
            !isValidForm && companyName === '' &&
            <FormHelperText>Field is required.</FormHelperText>

          }
        </FormControl>

        <Button variant="contained" sx={{
          backgroundColor: colors.primary,
          borderRadius: 50,
          padding: '10px 10px',
          marginTop: '30px',
          width: '40vw',
          alignSelf: 'center'
        }}
          onClick={() => {
            companyName === '' ? setValidation(false)
              : handleCompanyClick(companyName)
          }}
        >Confirm</Button>
      </Box>
    </>
  );

  //   <div>
  //     <h1>Login</h1>
  //     {error && <div style={{ color: 'red' }}>{error}</div>} {/* Display error message if any */}
  //     <input
  //       type="text"
  //       placeholder="Username"
  //       value={username}
  //       onChange={(e) => setUsername(e.target.value)}
  //     />
  //     <input
  //       type="password"
  //       placeholder="Password"
  //       value={password}
  //       onChange={(e) => setPassword(e.target.value)}
  //     />
  //     <button onClick={handleLogin}>Login</button>
  //   </div>
  // );
}

export default SelectCompany;
