import React, { useState } from 'react';
import axios from 'axios';
import JobsProcessesPage from './JobsProcessesPage';
import { Box, Button, FormControl, Snackbar, TextField, Typography } from '@mui/material';
import colors from '../../styles/colors';
import messages from '../../constants/messages';
import Header from './components/header';
import { SnackbarProvider, useSnackbar } from 'notistack';

function LoginPage({ name, setAvailableCompanies, setIsLoggedIn, isLoggedIn }) {
  const [username, setUsername] = useState("RECON_AbellaJ23");
  const [password, setPassword] = useState('password');
  const { enqueueSnackbar } = useSnackbar();
  // const [isLoggedIn, setIsLoggedIn] = useState(false); // State to track login status

  const handleLogin = async () => {
    try {
      const response = await axios.post('https://api.milesahead.team/api/register', { name, username, password });

      if (response.data.error) {
        enqueueSnackbar(response.data.error, 'error');
        // setError(response.data.error); // Set error if registration fails
      }
      else if (
        response.data.message.saveCredentialsForUser &&
        response.data.message.saveCredentialsToSecrets.createdARN &&
        response.data.companies.length > 0
      ) {
        setAvailableCompanies(response.data.companies)
        setIsLoggedIn(true); // Set login status to true on success
        // window.location.href = 'https://kivensferrer.github.io/hide/apps/mock-opportunities/'
      }
    } catch (error) {
      const errorMessage = error.response.data.error;
      enqueueSnackbar(errorMessage, { variant: 'error' });
      setIsLoggedIn(false);
      console.error('Login failed', error);
    }
  };

  // If logged in, render the JobsProcessesPage component
  if (isLoggedIn) {
    return <JobsProcessesPage name={name} />;
  }

  return (
    <>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'stretch', }}>
        <Header title={messages.login.headerTitle}
          subTitle={messages.login.headerSubtitle}
        />
        <FormControl size='small' sx={{ p: '20px', alignSelf: 'center' }} display='flex' flexDirection={'column'}>
          <Typography variant="caption" color="textSecondary" fontWeight='bold'>
            UKG Username
          </Typography>
          <TextField
            sx={{ width: '70vw' }}
            variant="outlined"
            placeholder="Enter UKG Username"
            margin="dense"
            size="small"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </FormControl>
        <FormControl size='small' sx={{ p: '20px', alignSelf: 'center' }} display='flex' flexDirection={'column'}>
          <Typography variant="caption" color="textSecondary" fontWeight='bold'>
            UKG Password
          </Typography>
          <TextField
            sx={{ width: '70vw' }}
            variant="outlined"
            type='password'
            placeholder="Enter UKG Password"
            margin="dense"
            size="small"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </FormControl>
        <Button variant="contained" sx={{
          backgroundColor: colors.primary,
          borderRadius: 50,
          padding: '10px 10px',
          marginTop: '30px',
          width: '40vw',
          alignSelf: 'center'
        }}
          onClick={handleLogin}
        >Connect</Button>
      </Box>
    </>
  );

  // return (
  //   <div>
  //     <h1>Login</h1>
  //     {error && <div style={{ color: 'red' }}>{error}</div>} {/* Display error message if any */}
  //     <input
  //       type="text"
  //       placeholder="Username"
  //       value={username}
  //       onChange={(e) => setUsername(e.target.value)}
  //     />
  //     <input
  //       type="password"
  //       placeholder="Password"
  //       value={password}
  //       onChange={(e) => setPassword(e.target.value)}
  //     />
  //     <button onClick={handleLogin}>Login</button>
  //   </div>
  // );
}

export default LoginPage;
