import React, { useEffect, useState } from 'react';
import jsPDF from 'jspdf';
import axios from 'axios';
import { enqueueSnackbar } from 'notistack';
import dayjs from 'dayjs';

import Header from './components/header';
import { Box, Button, CircularProgress, IconButton, Stack, Typography } from '@mui/material';
import colors from '../../styles/colors';
import messages from '../../constants/messages';
import Paper from '@mui/material/Paper';
import FileDownloadIcon from '@mui/icons-material/FileDownload';

// Function to download the content as PDF
const downloadPDF = (createdDate, statuses) => {
  const doc = new jsPDF();
  const documentName = dayjs(createdDate).format('MMMM_DD_YYYY_HH:MMa') + '.pdf'
  let y = 10; // Starting Y position in the PDF

  doc.setFontSize(16);
  doc.text('Opportunity Creation Status', 10, y);
  y += 10;

  statuses.forEach((status, index) => {
    doc.setFontSize(12);
    doc.text(
      `${index + 1}. Job: ${status.job} | Process Name: ${status.processName} | Process: ${status.process} \n | Status: ${status.status}`,
      10,
      y
    );
    y += 10;
  });

  doc.save(documentName); // Download the PDF
};

const StatusesStack = ({ status, successfulStatuses, unSuccessfulStatuses }) => {

  return (
    <Paper elevation={2} sx={{ p: '10px', mt: '10px', border: 1, borderRadius: 4, width: '70vw', alignSelf: 'center' }}>
      <Stack
        direction="row"
        spacing={2}
        sx={{
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Box>
          <Typography variant="body1" component="div" sx={{ flexGrow: 1, fontWeight: '600' }}>
            {dayjs(status.createdAt).format('MMMM_DD_YYYY_HH:MMa') + '.pdf'}
          </Typography>
          <Typography variant="subtitle1" component="div" sx={{ flexGrow: 1, fontWeight: '500' }}>
            <b>{successfulStatuses}</b> successful | <b>{unSuccessfulStatuses}</b> failed
          </Typography>
        </Box>
        <Box sx={{
          backgroundColor: colors.primary, width: 30,
          height: 30, borderRadius: 50, p: '5px', display: 'flex', alignItems: 'center', justifyContent: 'center'
        }}>

          <IconButton onClick={() => downloadPDF(status.createdAt, status.statuses)}>
            <FileDownloadIcon sx={{ color: colors.white }} />
          </IconButton>
        </Box>
      </Stack>
    </Paper>
  );
}


function StatusPage({ statuses, resetProcess, name, company }) {
  const [allStatuses, setStatuses] = useState([]);

  // Function to navigate to JobsProcessesPage
  const goToJobsProcessesPage = () => {
    resetProcess()
  };

  useEffect(() => {
    const getStatuses = async () => {
      try {
        const response = await axios.get('https://api.milesahead.team/api/get-created-opportunities',
          {
            params: {
              name,
              company
            }
          });

        const statuses = response.data;
        setStatuses(statuses);
      } catch (error) {
        enqueueSnackbar('Error fetching statuses: ' + error, 'error');
      }

    }
    getStatuses();
  }, [statuses]);

  return (
    <>
      <Header title={messages.opportunityCreation}
      />
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'stretch', }}>
        <Typography variant="h5" component="div" sx={{ p: '20px', flexGrow: 1, alignSelf: 'center' }}>
          Opportunities
        </Typography>
        {statuses.length < 1 &&
          <Paper elevation={2} sx={{ p: '10px', mt: '20px', border: 1, borderRadius: 4, width: '70vw', alignSelf: 'center' }}>
            <Stack
              direction="row"
              spacing={2}
              sx={{
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <Box>
                <Typography variant="body1" component="div" sx={{ flexGrow: 1, fontWeight: '600' }}>
                  Generating File ...
                </Typography>
                <Typography variant="subtitle1" component="div" sx={{ flexGrow: 1, fontWeight: '500' }}>
                  0 successful | 0 failed
                </Typography>
              </Box>
              <Box sx={{
                backgroundColor: colors.primary, width: 30,
                height: 30, borderRadius: 50, p: '5px', display: 'flex', alignItems: 'center', justifyContent: 'center'
              }}>
                <CircularProgress size={'25px'} sx={{ color: colors.white }} />
              </Box>
            </Stack>
          </Paper>
        }
        {
          allStatuses.length > 0 &&
          allStatuses.reverse().map(status => {
            const successfulStatuses = status.statuses.length > 0
              ? status.statuses.filter(processStatus => processStatus.status === 'successful').length : 0;
            const unSuccessfulStatuses = (status.statuses.length - (successfulStatuses.length || 0)) || 0;

            return (
              <StatusesStack
                key={status.createdAt}
                status={status}
                successfulStatuses={successfulStatuses}
                unSuccessfulStatuses={unSuccessfulStatuses}
              />
            );
          })
        }
        <Button variant="contained" sx={{
          backgroundColor: colors.primary,
          borderRadius: 50,
          padding: '10px 10px',
          marginTop: '50px',
          marginBottom: '50px',
          width: '40vw',
          alignSelf: 'center'
        }}
          onClick={goToJobsProcessesPage}
          disabled={statuses.length < 1}
        >Create Opportunity</Button>
      </Box>
    </>
  );

  // return (
  //   <div>
  //     <h1>Opportunity Creation Status</h1>
  //     <ul>
  //       {statuses.map((status, index) => (
  //         <li key={index}>
  //           <strong>Job:</strong> {status.job} | <strong>Process Name:</strong> {status.processName} | <strong>Process:</strong> {status.process} | <strong>Status:</strong> {status.status}
  //         </li>
  //       ))}
  //     </ul>
  //     {/* Button to download the PDF */}
  //     <button onClick={downloadPDF}>Download as PDF</button>
  //     {/* Button to navigate to JobsProcessesPage */}
  //     <button onClick={goToJobsProcessesPage}>Go to Jobs Processes</button>
  //   </div>
  // );
}

export default StatusPage;
