import React, { useRef } from 'react';
import { Box, Button, Divider, Typography } from '@mui/material';
import colors from '../../styles/colors';
import messages from '../../constants/messages';
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import Footer from './components/footer';
import Opportunity from './components/addOpportunity';
import Header from './components/header';
import { ProcessTypes } from './JobsProcessesPage';

const JobProcesses = (props) => {
  const {
    jobs,
    processes,
    handlePairChange,
    handleAddPair,
    handleDeletePair,
    handleSubmit,
    pairs,
    processType,
    setProcessType
  } = props;
  const childRef = useRef()

  const headerTitle = processType === ProcessTypes.CREATE ? messages.opportunityCreation : messages.createOpportunity

  const updateProcessType = (type) => {
    setProcessType(type);
  }

  const publishJobProcess = () => {
    const emptyPairs = pairs.filter(pair => pair.job === '' || pair.process === '');

    emptyPairs.length > 0 ? childRef.current.validate() : handleSubmit();
  }

  return (
    <>
      <Box sx={{ pb: '80px' }}>
        <Header title={headerTitle} subTitle={messages.createOpportunitySubTitle} showSubTitle={processType === ProcessTypes.PUBLISH} />
        {processType === ProcessTypes.CREATE &&
          <Box style={{ padding: '10px 20px' }}>
            <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}>
              Opportunities
            </Typography>
            <Typography variant="subtitle1" component="div" sx={{ flexGrow: 1 }}>
              {messages.opportunitySubTitle}
            </Typography>
          </Box>
        }
        {processType === ProcessTypes.PUBLISH &&
          <>
            {Array.from({ length: pairs.length }, (_, index) => (
              <React.Fragment key={index}>
                <Opportunity
                  ref={childRef}
                  opportunityPair={pairs[index]}
                  numberOfOpportunities={index}
                  jobs={jobs}
                  processes={processes}
                  deletePair={handleDeletePair}
                  updatePair={handlePairChange}
                />
                {index < pairs.length - 1 && (
                  <Divider
                    variant="middle"
                    color={colors.primary}
                    sx={{ color: colors.primary, height: '1px', width: '71vw', mt: '15px' }} />
                )}
              </React.Fragment>
            ))}          </>
        }
        {processType === ProcessTypes.PUBLISH &&
          <Button
            startIcon={<AddCircleOutlineIcon />}
            sx={{ mt: 2, ml: 1, p: '5px 25px', color: colors.primary }}
            onClick={() => handleAddPair()}
          >
            Add Opportunity
          </Button>
        }
      </Box>
      <Footer
        setCurrentProcess={updateProcessType}
        processType={processType}
        handleSubmit={publishJobProcess}></Footer>
    </>
  )
}

export default JobProcesses;