import * as React from "react";
import { AppBar, Button, IconButton, Stack } from "@mui/material";
import colors from "../../../styles/colors";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import messages from "../../../constants/messages";
import { ProcessTypes } from "../JobsProcessesPage";

const Footer = (props) => {
  const {
    setCurrentProcess,
    processType,
    handleSubmit
  } = props;
  const buttonTitle = processType === ProcessTypes.CREATE ? messages.createOpportunity : messages.publish

  const onSubmit = () => {
    if (processType === ProcessTypes.PUBLISH) {
      handleSubmit();
    } else {
      setCurrentProcess(ProcessTypes.PUBLISH)
    }
  }

  const handleBack = () => {
    if (processType === ProcessTypes.CREATE) {
      setCurrentProcess(ProcessTypes.COMPANY)
    } else if (processType === ProcessTypes.PUBLISH) {
      setCurrentProcess(ProcessTypes.CREATE)
    }
  }

  return (
    <AppBar position="fixed"
      sx={{ top: 'auto', bottom: 0, padding: '10px 0', alignItems: 'center', backgroundColor: colors.white }}>
      <Stack direction="row" spacing={2}>
        <IconButton variant="outlined"
          onClick={handleBack}
          sx={{
            color: colors.primary,
            border: '1px solid',
            borderRadius: 2,
            padding: '0px 10px',
          }}
        ><ArrowBackIcon

            sx={{ color: colors.primary }} /></IconButton>
        <Button variant="contained" sx={{
          backgroundColor: colors.primary,
          borderRadius: 50,
          padding: '10px 10px',
          width: '60vw',
        }}
          onClick={onSubmit}
        >{buttonTitle}</Button>
      </Stack>
    </AppBar>
  );
};

export default Footer;