import { Box, Typography } from '@mui/material';
import React from 'react';
import colors from '../../../styles/colors';

const Header = (props) => {
  const {
    title,
    subTitle,
    showSubTitle = true
  } = props;

  return (
    <>
      <Box sx={{ backgroundColor: colors.secondary }}>
        <Typography variant="h5" component="div" sx={{ flexGrow: 1, color: colors.white, p: '20px', pb: '0px' }}>
          {title}
        </Typography>
        <div style={{ minHeight: 50, paddingLeft: '20px' }}>
          {showSubTitle &&
            <Typography variant="subtitle1" component="div" sx={{ flexGrow: 1, color: colors.white, paddingTop: '10px' }}>
              {subTitle}
            </Typography>
          }
        </div>
      </Box>
    </>
  );
}

export default Header;